html, body, #app {
  margin: 0;
  height: 100%;
  width: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

.container {
  min-width: 1200px;
  margin: 0 auto;
  padding: 50px;
  border: 1px solid #000;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-clear {
  border: none;
  background: transparent;
}

.nav-link {
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

.grid {
  display: flex;
  flex-wrap: wrap;
}

.space-around{
  justify-content: space-around;
}

.header-lg{
  font-size: 35px;
  font-weight: 300;
  margin: 20px;
}

.avatar{
  width: 150px;
  height: 150px;
  border-radius: 3px;
  margin: 0 auto;
  display: block;
}

.center-text{
  text-align: center;
}

.link{
  color: rgb(187, 46, 31);
  text-decoration: none;
  font-weight: bold;
}

.card-list{
  margin: 20px 0;
  font-size: 20px;
}

.card-list li{
  display: flex;
  align-items: center;
  margin: 10px;
}

.card-list svg{
  margin-right: 10px;
}

.card-list a{
  font-weight: 500;
  color: inherit;
}

.bg-light{
  background: rgba(0,0,0,0.08);
  border-radius: 3px;
}

.repo{
  margin: 10px 0;
  width: 250px;
  padding: 20px;
}

.repo a {
  text-decoration: none;
}

.repo img{
  margin-bottom: 8px;
}